import '../../styles/sections/cart-upsell.scss';

import { cartAdd } from '@process-creative/slate-theme-tools';
import { delegate } from '../tools/utilities/event-tools';
import { CLASS_DISABLED } from '../tools/constants/classes';

const SELECTOR_FORM = '[data-cart-upsell-form]';
const SELECTOR_VARIANT_SELECTOR = '[data-cart-upsell-selector]';
const SELECTOR_BTN = '[type="submit"]';

delegate({
  element: document,
  event: 'submit',
  selector: SELECTOR_FORM,
  callback: async (e,form:HTMLFormElement) => {
    const vs = form.querySelector<HTMLInputElement>(SELECTOR_VARIANT_SELECTOR);
    const btn = form.querySelector<HTMLButtonElement>(SELECTOR_BTN);
    if(!vs) return;

    e.preventDefault();

    const id = parseInt(vs.value);
    if(btn) {
      btn.disabled = true;
      btn.classList.add(CLASS_DISABLED);
    }

    await cartAdd({
      items: [ { id, quantity: 1 } ]
    });

    
    if(btn) {
      btn.disabled = false;
      btn.classList.remove(CLASS_DISABLED);
    }
  }
});